.profile {
  display: flex;
  justify-content: left;
  align-items: center;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 51px;
  gap: 30px;
}

@media screen and (max-width: 767px) {
  .profile {
    flex-direction: column;
    padding-top: 42px;
    padding-bottom: 36px;
  }
}