.card__photo {
  width: 100%;
  aspect-ratio: 1/1;
  object-fit: cover;
  object-position: center;
  border-radius: 10px 10px 0 0;
  display: block;
  cursor: pointer;
  color: #fff;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  -webkit-tap-highlight-color: transparent;
}